.songs-result {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.songs-result h1 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: #333;
}

.songs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}
