.app-header {
  background: #2c3e50;         
  color: #fff;
  padding: 10px 0;            
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.header-container {
max-width: 1200px;
margin: 0 auto;
padding: 0 20px;
display: flex;
justify-content: space-between;
align-items: center;
}

.app-logo a {
  text-decoration: none;
  color: #fff;
  font-size: 1.8rem;
}

.header-nav a {
  margin-left: 20px;
  text-decoration: none;
  color: #fff;
  font-size: 1.1rem;
}

.header-nav a:hover {
  text-decoration: underline;
}

@media (max-width: 600px) {
  .header-container {
    padding: 0 10px;
  }

  .app-logo a {
    font-size: 1.5rem;
  }

  .header-nav a {
    font-size: 0.95rem;
    margin-left: 10px;
  }
}
